import React from 'react';
import '../../styling/pinStyles.scss';
import styled from 'styled-components'


class Pin extends React.Component<any, any> {
    showSmallPin() {
        return (
            <SmallPin>
                <SmallTop/>
                <SmallBottom/>
            </SmallPin>
        );
    }

    showLargePin() {
        return (
            <LargePin className={this.props.className}>
                <LargeTop/>
                <LargeBottom/>
            </LargePin>
        );
    }

    render() {
        return this.showSmallPin()
    }
}

const SmallPin = styled.div`
    height: 20px;
    width: 16px;
    margin-left: auto;
    margin-right: auto;
`;
const SmallTop = styled.div`
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      border-radius: 50%;
      border: 6px solid $border-color;
      width: 18px;
      height: 18px;
`;

const SmallBottom = styled.div`
 width: 0;
      height: 0;
      margin-top: -5px;
      margin-left: 1px;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 12px solid $border-color;
`;

const LargePin = styled.div`
    height: 30px;
    width: 22px;
    margin-left: auto;
    margin-right: auto;
`;

const LargeTop = styled.div`
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-radius: 50%;
    border: 8px solid $border-color;
    width: 24px;
    height: 24px;
`;

const LargeBottom = styled.div`
    width: 0;
    height: 0;
    margin-top: -7px;
    margin-left: 1px;
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-top: 15px solid $border-color;
`;

export default Pin;